body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  box-sizing: border-box;
}
.App {
  text-align: center;
}
.addContact {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    -webkit-transform: translate(0, 52px);
            transform: translate(0, 52px);
    background: rgb(231, 231, 231);
}
.row {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}
.left {
    flex: 1 1;
    text-align: right;
}
.right {
    flex:1 1;
    text-align: left;
}
.errors {
    width: 100%;
    min-height: 57px;
}
.error {
    display: block;
    margin: 1px;
    padding: 2px 10px;
    border: 1px solid red;
    font-size: 14px;
    background-color: rgb(248, 159, 159);
}
.addContact input {
    padding: 5px 10px;
    border: 0;
    margin: 5px 0;
}
.addContact button {
    padding: 10px 0;
    margin: 5px;
    width: 70px;
    border: 0;
    border-radius: 5px;
    cursor: pointer;
    transition: 0.2s;
}
.confirm {
    background: rgb(21, 172, 21);
}
.cancel {
    background: rgb(255, 92, 92);
}
.confirm:hover {
    background: green;
}
.cancel:hover {
    background: red;
}
.searchbar {
    padding: 11px;
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
    border: 0;
}
.searchbox {
    display: flex;
    justify-content: space-between;
    padding: 10px;
}
.icon {
    display: inline-block;
    color: rgb(63, 63, 63);
    background-color: white;
    font-size: 20px;
    padding: 5px;
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
    -webkit-transform: translate(0,4px);
            transform: translate(0,4px);
}
.button {
    padding: 11px 10px;
    border-radius: 5px;
    border: 0;
    background: white;
    color: rgb(63, 63, 63);
    -webkit-transform: translate(0, 4px);
            transform: translate(0, 4px);
    transition: 0.3s;
}
.button:hover {
    cursor: pointer;
    background: rgb(194, 193, 193);
}
.header {
    background-color: rgb(63, 63, 63);
    color: white;
    padding: 5px 0;
    position: fixed;
    height: 110px;
    left: 0;
    right: 0;
    top: 0;
}
.header-wrap {
    max-width: 800px;
    margin: 0 auto;
}
.title {
    margin: 0;
}

.contact {
    background: rgb(165, 165, 165);
    padding: 5px 10px;
    margin: 5px;
    display: flex;
    justify-content: space-between;
}
.contactInfo {
    display: flex;
}
.iconUser {
    font-size: 50px;
    padding: 0 10px;
}
.contactDeatils {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
}
.name {
    font-weight: bold;
    font-size: 20px;
}
.btns-nav {
    display: flex;
    align-items: center;
}
.btns-nav button {
    width: 60px;
    height: 30px;
    border: 0;
    margin: 3px;
    cursor: pointer;
    border-radius: 5px;
    transition: 0.2s;
}
.edit {
    background: orange;
}
.delete {
    background: red;
    color: white;
}
.edit:hover {
    background: rgb(163, 106, 1);
}
.delete:hover {
    background: rgb(190, 82, 20);
}
main {
    padding-top: 110px;
    margin: 0 auto;
    max-width: 800px;
}
